.navbar {
  margin-bottom: 0px;
}

.nonActive {
  background-color: transparent;
  color: gray;
}

.activeLabel {
  background-color: #EEEDEE;
}

.mode-label {
  padding: 12px;
  font-size: 12px;
  float: left;
  cursor: pointer;
  color: #0275D8;
}

.mode-label:hover {
  background-color: #F1F1F1;
  color: #0275D8;
}

.show-grid {
  border-top: 1px solid #ddd;
}

.duration-content {
  width: 3600px;
  height: 100%;
}

.cell-height {
  height: 25px;
  /*margin-bottom: 2px;*/
}

.task-content {
  width: 15%;
  display: inline-block;
  /*padding: 0% 2%;*/
  background-color: #FFFFFF;
  /*border-left: 1px solid #DDDDDD; */
  border-bottom: 1px solid #DDDDDD;
}

.footer-content {
  display: inline-block;
  /*padding: 0% 2%;*/
  background-color: #FFFFFF;
  border-right: 1px solid #DDDDDD; 
  border-bottom: 1px solid #DDDDDD;
}

.right-toolbar {
  position: absolute;
  top: -35px;
  right: 0px;
  width: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.duration-input {
  background-color: #ffffff;
  /*border-color: F1F1F1;*/
  width: 100%;
  border: 0px;
  text-align: center;
}

.duration-input:hover {
  /*border: 2px solid #7CFC00;*/
  border-radius: 3px;
}

.task-footer {
  background-color: #FFFFFF;
  /*padding-right: 10px;*/
  text-align: end;
  height: 30px;
}

.text-content {
  /*padding: 0px 20px;*/
  text-align: left;
}

.cell-height:focus {
  border: 1px solid black;
}

.timesheet-content {
  display: -ms-flexbox;
  display: flex;
}

.content-view {
  padding: 10px;
}

.cell-header {
  background-color: #FFFFFF;
}

.footer-context {
  text-align: center;
}

.addLine {
  background-color: #0275D8;
  border: none;
  padding: 5px 10px;
  margin-left: 10px;
  float: left;
}

.navigation {
  background-color: #0275D8;
  border: none;
  padding: 7px 12px;
}

.loader {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #3498db; /* Blue */
    border-radius: 50%;
    width: 120px;
    height: 120px;
    -webkit-animation: spin 2s linear infinite;
            animation: spin 2s linear infinite;
}

.sorting-component {
  padding-left: 5px;
}

.collapse-icon {
  margin-right: 5px;
  font-size: 13px;
  cursor: pointer;
}

@-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
    0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media only screen and (max-width: 992px) {
    .task-content {
      max-width: 30%;
      -ms-flex: 1 1 0%;
          flex: 1 1 0%;
    }
}



